.seButton{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 16px 24px;

    position: static;
    width: 221px;
    height: 50px;
    left: 0px;
    top: 0px;

    box-sizing: border-box;
    /* Shadow */

    filter: drop-shadow(0px 20px 50px rgba(191, 21, 108, 0.05));
    border-radius: 20px;
    background-color: #6160DC;
    /* Inside Auto Layout */

    flex: none;
    order: 0;
    flex-grow: 0;
    margin: 24px ;
    margin-left: 30px;

}
