span.switcher {
  top: 129px;
  left: 20px;
  position: absolute;
  width: 200px;
  height: 50px;
  border-radius: 25px;
  margin: 20px 0;
  font-family: Nunito;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 25px;
  text-align: center;
  border-radius: 24px;
  
}
span.switcher input {
  -webkit-appearance: none;
          appearance: none;
  position: relative;
  width: 200px;
  height: 50px;
  border-radius: 25px;
  background-color: #6160DC;
  outline: none;
  font-family: "Oswald", sans-serif;
}
span.switcher input:before,  span.switcher input:after {
  z-index: 2;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  color: #F5F5F5;
}
span.switcher input:before {
  content: "Receptionist";
  left: 20px;
}
span.switcher input:after {
  content: "Staff";
  right: 20px;
}
span.switcher label {
  z-index: 1;
  position: absolute;
  top: 10px;
  bottom: 10px;
  border-radius: 20px;
}

span.switcher.switcher-2 {
  overflow: hidden;
}
span.switcher.switcher-2 input {
  transition: background-color 0s 0.5s;
}
span.switcher.switcher-2 input:before {
  color: #1E1E1E;
}
span.switcher.switcher-2 input:after {
  color: #F5F5F5;
}
span.switcher.switcher-2 input:checked {
  background-color: #FFFFFF;
}span.switcher.switcher-2 input:checked + label {
  background: #FFFFFF;
  -webkit-animation: turn-on 0.5s ease-out;
          animation: turn-on 0.5s ease-out;
}
@-webkit-keyframes turn-on {
  0% {
    left: 100%;
  }
  100% {
    left: 0%;
  }
}
@keyframes turn-on {
  0% {
    left: 100%;
  }
  100% {
    left: 0%;
  }
}
 span.switcher.switcher-2 input:not(:checked) {
  background: #1E1E1E;
}
 span.switcher.switcher-2 input:not(:checked) + label {
  background: #1E1E1E;
  -webkit-animation: turn-off 0.5s ease-out;
          animation: turn-off 0.5s ease-out;
}
@-webkit-keyframes turn-off {
  0% {
    right: 100%;
  }
  100% {
    right: 0%;
  }
}
@keyframes turn-off {
  0% {
    right: 100%;
  }
  100% {
    right: 0%;
  }
}
 span.switcher.switcher-2 label {
  top: 0px;
  width: 200px;
  height: 50px;
  border-radius: 25px;
}

